/* src/components/mainpage/startnow/StartNow.css ( ) */

/* ----------------------------------------------------
   1. StartNow   (background-color )
   ---------------------------------------------------- */
.start-footer-integration-wrapper {
    /*       */
    background-color: #7a52c6;
    color: white;

    /*     flex  */
    display: flex;
    flex-direction: column;

    /*      content footer  */
    min-height: calc(100vh - 1px); /*    /       ( ) */
}

/* ----------------------------------------------------
   2.  StartNow   (  )
   ---------------------------------------------------- */
.start-content-container {
    flex-grow: 1; /*            */
    display: flex;
    flex-direction: column;
    justify-content: center; /*      */

    /*        */
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;

    padding: 3rem;
    box-sizing: border-box;
    text-align: center;
}

/* ----------------------------------------------------
   3.    (  )
   ---------------------------------------------------- */
.start-title {
    font-size: 40px;
    font-weight: bold;
    margin-bottom: 20px;
}

.start-description {
    font-size: 20px;
    max-width: 720px;
    margin: 0 auto 32px;
    line-height: 1.8;
}

.start-button {
    background-color: white;
    color: #7a52c6;
    padding: 16px 32px;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.2s ease;
    text-decoration: none;
    align-self: center; /*     */
}

.start-button:hover {
    background-color: #f2e9ff;
}

/* ----------------------------------------------------
   4.   ( )
   ---------------------------------------------------- */
.dark-mode .start-footer-integration-wrapper {
    background-color: #4e378a;
    color: #f5f5f5;
}

.dark-mode .start-button {
    background-color: #1e1e1e;
    color: #b88eff;
}

.dark-mode .start-button:hover {
    background-color: #2a2a2a;
}