.demo-section {
    /* height: 100vh; :        */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #f9f9f9;
    text-align: center;
    padding: 3rem;
    box-sizing: border-box;
    /*  :        ,      */
    min-height: 100vh;
}

.demo-title {
    font-size: 36px;     /*  */
    font-weight: bold;
    margin-bottom: 16px;
}

.demo-subtitle {
    font-size: 20px;     /*  */
    color: #666;
    margin-bottom: 48px;
}

.demo-content {
    /*  : flex-wrap   flex-wrap: nowrap; . ( flex-wrap: wrap; ) */
    display: flex;
    gap: 32px;
    justify-content: center;
    /* flex-wrap: wrap; //      */
    flex-wrap: nowrap;
    /*  :       ( ) */
    width: 100%;
    max-width: 1400px; /*        */

}

.demo-box {
    /*  :         (640px -> 480px   ) */
    width: 480px;
    height: 400px;
    padding: 24px;
    border-radius: 16px;
    background-color: white;
    border: 1px solid #e0e0e0;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

    /* flex-shrink: 0;          */
    flex-shrink: 0;
}

.placeholder-box strong {
    font-size: 18px;     /*  */
    color: #888;
}

/*   */
.dark-mode .demo-section {
    background-color: #181818;
    color: #f0f0f0;
}

.dark-mode .demo-subtitle {
    color: #aaa;
}

.dark-mode .demo-box {
    background-color: #2a2a2a;
    border: 1px solid #444;
}

.dark-mode .placeholder-box strong {
    color: #bbb;
}