/* ========== : Light ========== */

.broadcast-wrapper {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background-color: #f7f7f9; /*   */
    color: #222;
    padding-top: 70px;
}

.main-section {
    flex: 1;
    display: flex;
    overflow: hidden;
}

/*       */
.main-section.with-chat {
    grid-template-columns: 280px 1fr 340px; /* :  */
}

/*     */
.editor-area {
    flex: 1;
    min-width: 0;
    min-height: 0;
    background: #ffffff;            /*    */
    border-left: 1px solid #e6e6ea; /*   */
    display: flex;                  /*     */
    position: relative;
    padding: 0;
}

/*  (empty)  */
.empty-state {
    position: absolute;
    inset: 0;
    display: flex;                 /* grid  flex */
    flex-direction: column;        /*   */
    align-items: center;
    justify-content: center;
    gap: 8px;                      /*    */
    user-select: none;
}

.plus-button {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: none;
    background: #6f42c1;  /*  */
    color: #fff;
    font-size: 50px;
    cursor: pointer;
    box-shadow: 0 6px 16px rgba(111,66,193,0.25);
    transition: transform 0.15s ease, filter 0.15s ease, background 0.2s ease;
}
.plus-button:hover {
    transform: scale(1.06);
    filter: brightness(1.05);
}

.empty-help {
    margin: 0;                     /*   ! */
    color: #555;
    font-size: 15px;
    letter-spacing: 0.2px;
    opacity: 0.9;
}

/* ========== Dark Mode Overrides ========== */

.dark-mode .broadcast-wrapper {
    background-color: #111;
    color: #eee;
}

.dark-mode .editor-area {
    background: #0b0b0f;
    border-left: 1px solid rgba(255,255,255,0.06);
}

/*  /: CodeEditor       */
/* CodeEditor  Monaco  , CodeEditor     */
.dark-mode .code-editor {
    background-color: #0f0f1a;
    color: #fff;
}


.dark-mode .plus-button {
    background: #3a2a55;
    box-shadow: 0 6px 16px rgba(0,0,0,0.35);
}
.dark-mode .plus-button:hover {
    transform: scale(1.05);
    filter: brightness(1.05);
}

.dark-mode .empty-help {
    color: #c8c1d9;
    font-size: 14px;
}

/*   wrapper  () */
.broadcast-wrapper:fullscreen {
    padding-top: 0;           /*     0,    */
    background: #000;         /*    () */
}

/* Safari(WebKit)  */
:-webkit-full-screen .broadcast-wrapper {
    padding-top: 0;
    background: #000;
}

/*      */
.broadcast-wrapper:fullscreen .main-section {
    height: 100vh;            /*      */
}
.broadcast-wrapper:fullscreen .editor-area {
    padding: 0;
}
.broadcast-wrapper:fullscreen .code-editor {
    padding: 0;
}

/*   (  /  ) */
.preview-strip.nochrome {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 14px;
    width: min(1100px, 88%);
    max-width: 88%;
    height: 140px;                 /*   */
    border-radius: 14px;
    background: rgba(255,255,255,0.85);  /*   ( ) */
    box-shadow: 0 10px 26px rgba(0,0,0,0.18);
    border: 1px solid rgba(0,0,0,0.06);
    overflow: hidden;              /*    */
    z-index: 4;
    pointer-events: auto;
    backdrop-filter: blur(8px);
    -webkit-backdrop-filter: blur(8px);
}

/*  */
.dark-mode .preview-strip.nochrome {
    background: rgba(16,16,22,0.86);
    border: 1px solid rgba(255,255,255,0.06);
    box-shadow: 0 10px 26px rgba(0,0,0,0.45);
}

/*    :   ,   */
.preview-strip-scroller {
    height: 100%;
    width: 100%;
    overflow-x: auto;
    overflow-y: hidden;
    display: flex;
    align-items: center;     /*    */
    gap: 10px;
    padding: 10px 12px;
    scroll-snap-type: x proximity; /* :  */
}

/* ()   */
.preview-strip-scroller::-webkit-scrollbar { height: 8px; }
.preview-strip-scroller::-webkit-scrollbar-thumb { background: rgba(0,0,0,0.18); border-radius: 999px; }
.dark-mode .preview-strip-scroller::-webkit-scrollbar-thumb { background: rgba(255,255,255,0.18); }

/*  CodePreviewList   :
     /    */
.preview-strip-scroller > * {
    scroll-snap-align: center;         /*    */
}

/*        */
.editor-area .code-editor { padding-bottom: 170px; }

/*  / :     */
.preview-toggle {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 8px;                      /*   (   ) */
    padding: 6px 12px;
    font-size: 12px;
    line-height: 1;
    border: 1px solid rgba(0,0,0,0.08);
    border-radius: 999px;
    background: rgba(255,255,255,0.9);
    backdrop-filter: blur(8px);
    cursor: pointer;
    z-index: 1001; /*   (1000)    */
    box-shadow: 0 8px 18px rgba(0,0,0,0.18);
}

.preview-toggle.open { bottom: 160px; }  /*        */
.dark-mode .preview-toggle {
    border: 1px solid rgba(255,255,255,0.06);
    background: rgba(22,22,28,0.9);
}

/*      () */
.broadcast-wrapper:fullscreen .preview-toggle.open { bottom: 150px; }
.broadcast-wrapper:fullscreen .preview-strip.nochrome { bottom: 8px; }

/* ================================================= */
/*       (Controls Bar)            */
/* ================================================= */

.broadcast-controls-bar {
    position: fixed;
    bottom: 20px;
    right: 20px;
    left: auto;
    transform: none;

    display: flex;
    /*  :      */
    justify-content: flex-end;
    align-items: center;

    padding: 0; /*   */
    z-index: 1000;

    background: transparent; /*   */
    box-shadow: none; /*   */
}

.left-controls, .right-controls {
    display: flex;
    align-items: center;
    gap: 8px;
    flex-grow: 0;
}

.left-controls {
    justify-content: flex-end; /*    */
}

/*     */
.control-btn {
    display: flex;
    align-items: center;
    /*  :   (6px 12px -> 8px 16px) */
    padding: 8px 16px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    /*  :    */
    font-size: 16px;
    font-weight: 600;
    transition: background-color 0.2s, opacity 0.2s;
    white-space: nowrap;
}

.control-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.control-btn .icon {
    /*  :    */
    margin-right: 8px; /*    */
    font-size: 20px;
}

/*  : .share-toggle-btn     */
.share-toggle-btn,
.share-screen {
    background-color: #6a0dad; /*   (  ) */
    color: white;
}

.share-toggle-btn:not(:disabled):hover,
.share-screen:not(:disabled):hover {
    background-color: #c9a7ff;
}

/*    ( ,  ) */
.share-toggle-btn.active,
.share-screen.active {
    background-color: #38a75e; /*    */
}

.share-toggle-btn.active:not(:disabled):hover,
.share-screen.active:not(:disabled):hover {
    background-color: #2e8b4c;
}

.share-toggle-btn .check-icon,
.share-screen .check-icon {
    /*  :        */
    font-size: 16px;
    margin-right: 8px;
}


/*    */
.dark-mode .broadcast-controls-bar {
    background: transparent;
}

/*        */
.broadcast-wrapper:fullscreen .broadcast-controls-bar {
    bottom: 10px;
    right: 10px;
}