/* ========== : Light ========== */

.code-editor {
    flex: 1;
    min-width: 0;
    min-height: 0;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    padding: 16px;
    color: #222;
    font-family: 'Courier New', monospace;
    font-size: 15px;
}

.editor-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
    gap: 12px;
}

/*    */
.current-user-badge {
    background-color: #f2f2f4;
    color: #333;
    padding: 6px 12px;
    border-radius: 12px;
    font-weight: 600;
    font-size: 14px;
    border: 1px solid #e4e4ea;
    display: inline-flex;
    align-items: center;
    gap: 6px;
}

/*    */
.toolbar-right {
    display: inline-flex;
    align-items: center;
    gap: 8px;
}

/*   */
.run-button {
    background-color: #6a0dad;
    color: white;
    border: none;
    padding: 6px 12px;
    border-radius: 6px;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    align-items: center;
    gap: 6px;
    transition: background-color 0.2s ease;
}
.run-button:hover { background-color: #8844dd; }

/*   */
.viz-button {
    background-color: #f2f2f4;
    color: #333;
    border: 1px solid #e4e4ea;
    padding: 6px 12px;
    border-radius: 6px;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    align-items: center;
    gap: 6px;
    transition: background-color 0.2s ease, border-color 0.2s ease, color .2s ease;
}
.viz-button:hover {
    background-color: #ece9f7;
    border-color: #d7c7ff;
    color: #4f2aa3;
}

/*  [ ]      */
.toolbar-right .select-file-btn {
    /* run-button, viz-button      */
    padding: 6px 12px;

    /*    (CodecastLive.js  ) */
    background-color: #555;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    align-items: center;
    gap: 6px;
}
.toolbar-right .select-file-btn:hover {
    background-color: #777;
}


/* Monaco      */
.code-editor > div, /* @monaco-editor/react  */
.code-editor .monaco-editor,
.code-editor .monaco-editor .overflow-guard {
    flex: 1;
    min-height: 0;
}

/* ========== Dark Mode Overrides ========== */

/*  : CodeEditor   #1c1c1c  */
.dark-mode .code-editor {
    background-color: #1c1c1c;
    color: #fff;
}

/*  : Monaco Editor    #1c1c1c   */
.dark-mode .monaco-editor,
.dark-mode .monaco-editor .lines-content,
.dark-mode .monaco-editor .view-overlays,
.dark-mode .monaco-editor .scroll-decoration {
    background-color: #1c1c1c !important;
}

/*  : Gutter(  )     #1c1c1c  */
.dark-mode .monaco-editor .monaco-editor-background,
.dark-mode .monaco-editor .margin {
    background-color: #1c1c1c !important;
}

/*  :      */
.dark-mode .monaco-editor .line-numbers {
    color: #ffffff !important;
}


.dark-mode .current-user-badge {
    background-color: #222;
    color: #fff;
    border-color: #3a3a48;
}

.dark-mode .viz-button {
    background-color: #2b2b34;
    color: #eaeaf2;
    border: 1px solid #3a3a48;
}
.dark-mode .viz-button:hover {
    background-color: #34343f;
    border-color: #6a0dad;
    color: #c9a7ff;
}